#root{
    margin:0 10vw 0 10vw;
}
.network-name{
    font-size:calc(16px + 4vw);
    color:green;
}
.network-address{
    font-size:calc(5px + 1vw);
}
.flex-row{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction:row;
        flex-direction:row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.flex-col{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction:column;
        flex-direction:column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
}
.station{
    width:100%;
    height:50vh;
    min-height:250px;
    border-bottom-right-radius: 25px;
    margin-bottom:20px;
    -webkit-box-shadow: 10px 10px 5px #888888;
            box-shadow: 10px 10px 5px #888888;
}

.station-header{
    width:100%;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    height:20%;
    font-size:20px;
    background-color:green;
    color:white;
}
.station-header span{
    padding:2%;
}
.station-container{
    width:100%;
    height:80%;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    background-color:#DEE5E4;
    
    
}
.station-footer{
    width:100%;
    height:5%;
    padding-bottom:2px;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    background-color:#DEE5E4;
    border-bottom-right-radius: 25px;
}
.station-footer span{
    
}

.station-info{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction:column;
        flex-direction:column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width:40%;
    max-width:200px;
    padding:5px;
}
.bike-info{
    margin-top:5px;
    text-transform: uppercase;
    font-size:calc(12px + 1vw);
    color:green;
}
.bike-number{
  position: relative;
  display: inline-block;
  width: 70%;
  height: 0;
  padding: 35% 0;
  border:2px green solid;
  border-radius: 50%;
  line-height: 0;
  color: green;
  font-family: Helvetica, Arial Black, sans;
  font-size: 48px;
  text-align: center;
  background-color:white;
}
.bike-number:hover{
    background-color:green;
    color:white;
    -webkit-box-shadow: 10px 10px 5px #888888;
            box-shadow: 10px 10px 5px #888888;
    font-size:80px;
}

@media only screen and (max-width: 350px) {
    .station-info{
        width:100vw;
    }
    .network-header{
        display:-ms-flexbox;
        display:flex;
        -ms-flex-direction:column;
            flex-direction:column;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
    }
    .network-name{
        font-size:40px;
    }
    .network-address{
       font-size:12px;
    }
    .flex-row{
        -ms-flex-direction:column;
            flex-direction:column;
        
    }
    .bike-info{
        font-size:16px;
    }
    .bike-number{
        width:36%;
        height:25%;
        font-size:18px;
        padding:18% 0;
    }
    .bike-number:hover{
        font-size:36px;
    }
   
}


